body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  padding: 0px;
  margin: 0px;
  list-style: none;
  border:0;
  outline: 0;
  box-sizing: border-box;
}
html,body{
  height: 100%;
}
#root{
  height: 100%;
  display: flex;
}
.sidebar{
  width: 320px;
  max-width: 320px;
  border-right: 1px solid #ccc;
  padding: 25px;
}

.logo {
  margin-bottom: 25px;
  font-size: 25px
}
.description{
  color:rgb(112, 110, 110);
  margin-bottom: 25px;
  line-height: 150% ;

}
ul li strong {
  display: block;
  font-weight: 600;
  color:rgb(10, 10, 10);
  padding: 5px;
  cursor: pointer;
}
ul li strong:hover{
  color:#0099e5;
}
.content{
  flex: 1;
  overflow: auto;
}
.header{
  height: 50px;
  border-bottom: 1px solid #ccc;
  display: flex;
  position: sticky;
  top:0;
}
header .search{
  flex:1;
  position: relative;
}
.search .icon{
  position: absolute;
  top:0;
  left:0;
  width: 60px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon svg{width: 20px;height: 20px;}
.search input{
  width: 100%;
  height: 100%;
  font-size: 16px;
  padding:0px 20px 0px 60px;
}
input::placeholder{color: rgb(190, 186, 186);}

.brands{
  padding:25px;
  background-color: #f8f8f8;
  
}
.brand{
  border: 1px solid #ccc;
  background-color: #fff;
  padding:20px;
  margin-bottom: 10px; 
}
.brand h5{
  font-family: 'Quicksand', sans-serif;
  font-size: 15px;
  margin-bottom: 10px;
}
.brand-colors{
  display: flex;
}
.brand-colors span{
  background: var(--bgColor);
  height: 40px;
  text-align: center;
  justify-content: center; 
  margin-right: 7px;
  font-size: 15px;
  /* color: var(--textColor); */
  text-indent: -9999px;
  width: 70px;
  border:0.2px solid rgb(238, 237, 237);
  cursor: pointer;
}
.copied{
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #0099e5; 
  color:#fff;
  padding: 15px 25px;
  border-radius: 4px;
  font-size: 16px;
}
/*Aş. dakiler şeçili Brandlerin özellikleri*/
.brand-selected{
  border: 2px solid #0099e5;
  background-color: #fff;
  padding:22px;
  margin-bottom: 10px;
}

.brand-selected h5{
  font-family: 'Quicksand', sans-serif;
  font-size: 18px;
  margin-bottom: 10px;
  font-weight: 700;
}
.brand-colors-selected{
  display: flex;
}
.brand-colors-selected span{
  background: var(--bgColor);
  height: 40px;
  text-align: center;
  justify-content: center; 
  margin-right: 7px;
  font-size: 15px;
  color: var(--textColor);
  text-indent: -9999px;
  border:0.2px solid rgb(238, 237, 237);
  flex:1;
  cursor: pointer;
}
.brand-colors-selected span:hover{text-indent: 0px;}
/* .brand-colors-selected span:hover{text-indent: 0px;} */

.about-modal {
  max-width: 720px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 50px;
  position:relative;
  
}
.about-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, 0.4);
  align-items: center;
  justify-content: center;
  display: flex;

}
.about-modal h3{
  margin-bottom: 15px;
  font-size: 25px;
}
.about-modal p{
  line-height: 150% ;
  font-size: 14px;
  margin-bottom: 10px;
}
.modal-close-btn{
  position: absolute;
  right: 20px;
  top:20px;
  background: transparent;
  cursor: pointer;
}
.modal-close-btn svg{
  width: 18px;
  height: 18px;
}